<template>
  <div class="energyTrendAnalysis">
    <div class="left border-box tree">
      <el-tree
        :default-expand-all="true"
        :expand-on-click-node="false"
        :data="data"
        :props="defaultProps"
        @node-click="nodeClick"
      ></el-tree>
    </div>
    <div class="right border-box">
      <div class="search chartSearch">
        <div>
          <span>能源类型</span>
          <el-select
            v-model="energyVal"
            placeholder=""
            @change="energyTrendFun"
          >
            <el-option
              v-for="item in energyOption"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <el-date-picker
            v-model="date1"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="energyTrendFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="color: #00b4fe">至</div>
        <div>
          <el-date-picker
            v-model="date2"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="energyTrendFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div class="tabAll">
          <div
            v-for="(item, index) in timeTab"
            :key="index"
            :class="{ isActive: index == tTIndex }"
            @click="changeTimeTab(index)"
          >
            {{ item }}
          </div>
        </div>
      </div>
      <div class="tabel">
        <el-table :data="tableData" style="width: 100%" height="250">
          <el-table-column prop="CompanyId" label="企业编号" width="150">
          </el-table-column>
          <el-table-column prop="CompanyName" label="企业名称" width="170">
          </el-table-column>
          <el-table-column prop="EnergyTypeStr" label="能源类型">
          </el-table-column>
          <el-table-column prop="UseQty" label="当前用量"> </el-table-column>
          <el-table-column prop="PlanNum" label="产值计划值"> </el-table-column>
          <el-table-column prop="UsePlanNum" label="用量计划值">
          </el-table-column>
          <el-table-column prop="QOQ" label="环比"> </el-table-column>
          <el-table-column prop="UseQtyRatio" label="用量占比">
          </el-table-column>
          <el-table-column prop="UnitId" label="单位"> </el-table-column>
        </el-table>
      </div>
      <div class="border-box chart" style="flex-grow: 1">
        <e-Chart :arry="arry"></e-Chart>
      </div>
    </div>
  </div>
</template>
<script>
import eChart from "./components/echart.vue";
import { places, energyTrend } from "@/network/energyTrendAnalysis";
export default {
  components: {
    eChart,
  },
  data() {
    return {
      timeTab: ["日", "月", "年"],
      tTIndex: 0,
      data: [],
      defaultProps: {
        children: "children",
        label: "label",
      },
      energyOption: [
        "电",
        "冷凝水",
        "自来水",
        "蒸汽",
        "天然气",
        "氮气",
        "压缩空气",
        "中水"
      ],
      energyVal: "电",
      date1: "",
      date2: "",
      tableData: [],
      arry: {},
    };
  },
  mounted() {
    this.date2 = this.date1 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
    this.placesFun();
  },
  methods: {
    placesFun() {
      places()
        .then((res) => {
          let flag = false;
          res.content.forEach((item) => {
            let idFirst = item.id.substring(0, 1);
            if (idFirst == "A" || idFirst == "C") {
              item["isClick"] = true;
              if (!flag) {
                this.areaId = item.id;
                flag = true;
              }
            } else {
              item["isClick"] = false;
            }
          });
          this.data = this.$arrToTree(res.content);
          console.log(this.data)
          this.energyTrendFun();
        })
        .catch((err) => {});
    },
    energyTrendFun() {
      energyTrend(
        this.timeTab[this.tTIndex],
        this.energyVal,
        this.date1,
        this.date2,
        this.areaId
      )
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            this.tableData = content;
            let VAL = [];
            let VAL1 = [];
            let VAL2 = [];
            let VAL3 = [];
            content.forEach((item) => {
              VAL.push(item.CompanyName);
              VAL1.push(item.PlanNum);
              VAL2.push(item.UsePlanNum);
              VAL3.push(item.UseQty);
            });
            this.arry = {
              VAL: VAL,
              VAL1: VAL1,
              VAL2: VAL2,
              VAL3: VAL3,
            };
          }
        })
        .catch((err) => {});
    },
    changeTimeTab(index) {
      this.tTIndex = index;
      this.energyTrendFun();
    },
    nodeClick(item) {
      if (item.isClick) {
        this.areaId = item.id;
        this.energyTrendFun();
      }
    },
  },
};
</script>
<style scoped lang="less">
