import { request } from "./request";
import Vue from 'vue'

export function places() {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/places",
        method: 'POST'
    })
}
export function elecPlaces() {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecDrawAction/places",
        method: 'POST'
    })
}
export function elePlaces() {
    return request({
        // url: "/web/apps/gongqi.hsb.elec/elecTwoAction/places",
        method: 'POST'
    })
}
export function pvPlaces() {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvStationTwoAction/places",
        method: 'POST'
    })
}
export function energyTrend(dateType,energyType,fromDate,toDate,id) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/energyTrend?dateType="+dateType+'&energyType='+energyType+'&fromDate='+fromDate+'&id='+id+'&toDate='+toDate,
        method: 'POST'
    })
}
export function energyBalance(dateType,energyType,fromDate,toDate,id) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/energyBalance?dateType="+dateType+'&energyType='+energyType+'&fromDate='+fromDate+'&id='+id+'&toDate='+toDate,
        method: 'POST'
    })
}

