<template>
  <div class="Pechart">
    <div class="echart" ref="echart"></div>
  </div>
</template>
<script>
export default {
  props: {
    arry: {
      type: Object,
      default: () => [],
    },
  },
  data() {
    return {};
  },
  watch: {
    arry(val) {
      this.setEchart();
    },
  },
  mounted() {
  },
  methods: {
    setEchart() {
      let chart = this.$refs.echart;
      let myChart = this.$echarts.init(chart);
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        legend: {
          textStyle: {
            color: "#fff",
          },
        },
        grid: {
          left: "3%",
          right: "5%",
          bottom: "2%",
          top: "10%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: true,
            data: this.arry.VAL,
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            axisTick: {
              show: false,
            },
          },
        ],
        yAxis: [
          {
            // name: "单位：吨CO2",
            // nameLocation: "end",
            // nameTextStyle: {
            //   color: "#fff",
            //   padding: [0, 10, 0, 0],
            // },
            // nameGap: 15,
            type: "value",
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ["rgba(255, 255, 255, 0.5)"],
                type: "dashed",
              },
            },
          },
        ],
        series: [
          {
            name: "产值计划值",
            type: "bar",
            z: 1,
            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "rgba(0, 253, 152, 0.8)" }, //柱图渐变色
                  { offset: 1, color: "rgba(0, 252, 235, 0.8)" }, //柱图渐变色
                ]),
              },
            },
            data: this.arry.VAL1,
          },
          {
            name: "用量计划值",
            type: "bar",
            z: 1,
            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "rgba(0, 235, 255, 0.8)" }, //柱图渐变色
                  { offset: 1, color: "rgba(0, 58, 255, 0.8)" }, //柱图渐变色
                ]),
              },
            },
            data: this.arry.VAL2,
          },
          {
            name: "当前用量",
            type: "line",
            z: 2,
            emphasis: {
              focus: "series",
            },
            lineStyle: {
              normal: {
                color: "#FF9C00",
              },
            },
            itemStyle: {
              normal: {
                color: "#FF9C00",
              },
            },
            data: this.arry.VAL3,
          },
        ],
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>
<style scoped lang="less">
.echart {
  flex-grow: 1;
}
.Pechart {
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>